.Suc {
  background-image: url(../../Images/prfct.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  overflow: hidden;
}

.Success {
  width: 50%;
  border-radius: 20px;
  display: inline-block;
  padding: 30px;
  margin-top: 100px;
  text-align: center;
  /* background-color: rgba(0, 0, 0, 0.8); */
  background: rgba(70, 71, 74, 0.95);
}
.Success h1 {
  font-size: 25px;
  font-weight: 100;
  font-family: monospace;
  /* color: rgb(214, 195, 18); */
  color: whitesmoke;
  margin-top: 20px;
  margin-bottom: 20px;
}
.Success h2 {
  font-size: 20px;
  font-weight: 200;
  font-family: sans-serif;
  margin-bottom: 20px;
  /* color: rgb(214, 195, 18); */
  color: white;
}

.Success button {
  padding: 10px 20px;
  border-radius: 30px;
  border: none;
  /* background-color: rgba(214, 195, 18, 0.7); */
  background-color: green;
  font-size: 15px;
  font-weight: 300;
  font-family: sans-serif;
  transition: 0.3s;
  color: white;
  margin: 20px;
}

.Success button:hover {
  transform: translateY(2px);
}
@media (max-width: 500px) {
  .Suc {
    min-height: 100vh;
    background-image: url(../../Images/prfct.jpg);
    padding: 0;
    margin: 0;
    overflow: hidden;
  }
  .Success {
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .Success h1 {
    padding: 20px;
    color: whitesmoke;
    font-size: 20px;
    font-weight: 200;
    font-family: sans-serif;
  }
  .Success h2 {
    color: whitesmoke;
    font-size: 15px;
    font-weight: 100;
    font-family: sans-serif;
  }
  .Success button {
    padding: 7px;
    border-radius: 30px;
    border: none;
    /* background-color: rgba(214, 195, 18, 0.7); */
    background-color: green;
    font-size: 15px;
    font-weight: 300;
    font-family: sans-serif;
    transition: 0.3s;
    color: white;
    margin: 20px;
  }
}
